<template>
    <!-- 集团产量信息 -->
    <div class="wrap-div" ref="wrapHeight">
        <div v-if="isday" ref="day_chart" class="bar-chart" :style = "{width:wrapWidth+'px',height:wrapHeight+'px'}"></div>
        <div v-if="ismon" ref="mon_chart" class="mon-chart" :style = "{width:wrapWidth+'px',height:wrapHeight+'px'}"></div>
        <div v-if="isyear" ref="year_chart" class="bar-chart" :style = "{width:wrapWidth+'px',height:wrapHeight+'px'}"></div>
    </div>
</template>
<script>
    const echarts = require('echarts/lib/echarts');
    require('echarts/lib/chart/bar');
    require('echarts/lib/chart/pie');
    require('echarts/lib/chart/line');
    require('echarts/lib/chart/gauge');
    require('echarts/lib/component/tooltip');
    require('echarts/lib/component/legend');
    export default {
        data() {
            return {
                isday:true,
                isyear:false,
                ismon:false,
                wrapHeight: 250,
                wrapWidth: 403,
                groupnameData:[],
                groupdateData:[],
                groupqtyData:[],
                groupmoeyeData:[],
                groupxlnameData:[],         
            }
        },
        activated(){
            this.init();
        },
        mounted() {
            this.init();
        },
        watch:{
            '$store.state.JTweekTextI':'getweekText'
        },
        methods: {
            init(){
                this.getweekText();
                this.getDivHeight();
            },
            getDivHeight(){
                this.wrapHeight = this.$refs.wrapHeight.offsetHeight;
                this.wrapWidth = this.$refs.wrapHeight.offsetWidth;
            },

            //切换
            getweekText(){
                let JTtext = 1;
                    JTtext = this.$store.state.JTweekTextI;
                if(JTtext === 3){
                    this.isday = false;
                    this.ismon = false
                    setTimeout(()=>{
                        this.isyear = true;
                    })
                    this.getlist();
                }else if(JTtext === 2){
                    this.isyear = false;
                    this.isday = false;
                    setTimeout(()=>{
                        this.ismon = true;
                    })
                    this.getlist();
                }else {
                    this.isyear = false;
                    this.ismon = false
                    setTimeout(()=>{
                        this.isday = true;
                    })
                    this.getlist();
                }
                
            },
            
            //获取数据
            getlist(){
                let tempData = [];
                let tempDataI = [];
                let tempDataII = [];
                let dataI = ['#32c5e9','#ffd95a'];
                let xldataI = ['#e062ae','#ff9f7f'];
                let dataII = ['#32c5e9','#ffd95a','#9fe6b8'];
                this.groupnameData = [];
                this.groupdateData = [];
                this.groupqtyData = [];
                this.groupmoeyeData = [];
                this.groupxlnameData = [];
                this.getAjax('/mktmfc/forms/getgroupyieldmsg?datetype='+this.$store.state.JTweekTextI)
                .then((result)=>{
                    if(result.status === 200){
                        tempData = result.data;
                        for(let i=0; i<tempData.length; i++){
                            if(this.$store.state.JTweekTextI === 2){
                                this.groupnameData.push(tempData[i].sysname);
                                this.groupxlnameData.push(tempData[i].sysname);
                                this.groupdateData = this.returnWeek('year');
                                this.groupqtyData.push({
                                        name:tempData[i].sysname+'(产量)',
                                        type:'line',
                                        barWidth: '30%',
                                        yAxisIndex: 1,
                                        itemStyle: {
                                            normal: {
                                                color: xldataI[i],
                                            }
                                        },
                                        lineStyle: {
                                            normal: {
                                                width: 3,
                                                shadowColor: 'rgba(0,0,0,0.4)',
                                                shadowBlur: 10,
                                                shadowOffsetY: 10
                                            }
                                        },
                                        data:this.returnReconstruction(tempData[i].senddate,tempData[i].sendqty,this.returnWeek('year'))[1].map(item=>{
                                            return (item/1000).toFixed(2)
                                        })
                                    })
                                    this.groupmoeyeData.push({
                                        name:tempData[i].sysname+'(销量)',
                                        type:'bar',
                                        barWidth: '25%',
                                        itemStyle: {
                                            normal: {
                                                color: dataI[i],
                                                shadowColor: 'rgba(0, 0, 0, 0.5)',
                                                shadowBlur: 10
                                            }
                                        },
                                        data:this.returnReconstruction(tempData[i].senddate,tempData[i].salesamounts,this.returnWeek('year'))[1].map(item => {
                                            return (item/10000).toFixed(2)
                                        })
                                    })
                            }else if(this.$store.state.JTweekTextI === 1){
                                this.groupnameData.push(tempData[i].sysname);
                                this.groupxlnameData.push(tempData[i].sysname);
                                this.groupdateData = this.returnWeek('TendaysNoyear');
                                this.groupqtyData.push({
                                        name:tempData[i].sysname+'(产量)',
                                        type:'line',
                                        barWidth: '30%',
                                        yAxisIndex: 1,
                                        itemStyle: {
                                            normal: {
                                                color: xldataI[i],
                                            }
                                        },
                                        lineStyle: {
                                            normal: {
                                                width: 3,
                                                shadowColor: 'rgba(0,0,0,0.4)',
                                                shadowBlur: 10,
                                                shadowOffsetY: 10
                                            }
                                        },
                                        data:this.returnReconstruction(tempData[i].senddate,tempData[i].sendqty,this.returnWeek('Tendays'))[1].map(item=>{
                                            return (item/1000).toFixed(2)
                                        })
                                })
                                this.groupmoeyeData.push({
                                        name:tempData[i].sysname+'(销量)',
                                        type:'bar',
                                        barWidth: '25%',
                                        itemStyle: {
                                            normal: {
                                                color: dataI[i],
                                                shadowColor: 'rgba(0, 0, 0, 0.5)',
                                                shadowBlur: 10
                                            }
                                        },
                                        data:this.returnReconstruction(tempData[i].senddate,tempData[i].salesamounts,this.returnWeek('Tendays'))[1].map(item => {
                                            return (item/10000).toFixed(2)
                                        })
                                    })
                            }else {
                                if(tempData[i].sendqty.length === 0){
                                    tempData[i].sendqty = [0];
                                };
                                if(tempData[i].salesamounts.length === 0){
                                    tempData[i].salesamounts = [0];
                                };
                                this.groupnameData.push(tempData[i].sysname)
                                tempDataI.push({
                                        value:(tempData[i].sendqty[0]/10000).toFixed(2), 
                                        name:tempData[i].sysname+'(产量)',
                                        itemStyle:{normal:{color: xldataI[i]}},
                                    })
                                tempDataII.push({
                                        value:(tempData[i].salesamounts[0]/10000).toFixed(2), 
                                        name:tempData[i].sysname+'(销量)',
                                        itemStyle:{normal:{color: dataI[i]}},
                                    })
                            }
                            
                        }
                        // console.log(111,[...this.groupqtyData,...this.groupmoeyeData])
                        // console.log(222,this.groupqtyData)
                        // console.log(333,this.groupdateData)
                    }
                })
                .then(()=>{
                    this.initEchart();
                    this.setEchart([...this.groupnameData,...this.groupxlnameData],this.groupdateData,[...this.groupqtyData,...this.groupmoeyeData],tempDataI,tempDataII);
                })
            },

            initEchart(){
                if(this.isyear){
                    this.year_chart = echarts.init(this.$refs.year_chart);
                }else if(this.ismon){
                    this.mon_chart = echarts.init(this.$refs.mon_chart);
                }else if(this.isday){
                    this.day_chart = echarts.init(this.$refs.day_chart);
                }
            },

                        //传参
            setEchart(groupnameData,groupdateData,groupqtyData,tempDataI,tempDataII) {
                //日
                if(this.day_chart){
                    this.day_chart.setOption({
                        tooltip : {
                            trigger: 'axis',
                            axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                                type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                            }
                        },
                        legend: {
                            data:["恒利1站(销量)", "恒利2站(销量)", "恒利1站(产量)", "恒利2站(产量)"],
                            right: '2%',
                        },
                        grid: {
                            left: '8%',
                            width:'85%',
                            height:'65%',
                            bottom: '10%',
                            containLabel: true
                        },
                        xAxis : [
                            {
                                type: 'category',
                                // axisTick: {
                                //     alignWithLabel: true
                                // },
                                data:groupdateData
                            }
                        ],
                        yAxis : [
                            {
                                type: 'value',
                                name:'万(元)'
                            },

                            {
                                type: 'value',
                                name:'千(方)'
                            },
                        ],
                        series : groupqtyData
                        // series : [
                        //     {
                        //         name:'恒利搅拌站(销量)',
                        //         type:'bar',
                        //         barWidth: '30%',
                        //         itemStyle: {
                        //             normal: {
                        //                 color: '#32c5e9',
                        //                 shadowColor: 'rgba(0, 0, 0, 0.5)',
                        //                 shadowBlur: 10
                        //             }
                        //         },
                        //         data:groupqtyData[2].data
                        //     },
                        //     {
                        //         name:'合辉搅拌站(销量)',
                        //         type:'bar',
                        //         barWidth: '30%',
                        //         itemStyle: {
                        //             normal: {
                        //                 color: '#ffd95a',
                        //                 shadowColor: 'rgba(0, 0, 0, 0.5)',
                        //                 shadowBlur: 10
                        //             }
                        //         },
                        //         data:groupqtyData[3].data
                        //     },
                        //     {
                        //         name:'恒利搅拌站(产量)',
                        //         type:'line',
                        //         barWidth: '45%',
                        //         yAxisIndex: 1,
                        //         itemStyle: {
                        //             normal: {
                        //                 color: '#9fe6b8'
                        //             }
                        //         },
                        //         lineStyle: {
                        //             normal: {
                        //                 width: 3,
                        //                 shadowColor: 'rgba(0,0,0,0.4)',
                        //                 shadowBlur: 10,
                        //                 shadowOffsetY: 10
                        //             }
                        //         },
                        //         data:groupqtyData[0].data
                        //     },

                        //     {
                        //         name:'合辉搅拌站(产量)',
                        //         type:'line',
                        //         barWidth: '45%',
                        //         yAxisIndex: 1,
                        //         itemStyle: {
                        //             normal: {
                        //                 color: '#e062ae'
                        //             }
                        //         },
                        //         lineStyle: {
                        //             normal: {
                        //                 width: 3,
                        //                 shadowColor: 'rgba(0,0,0,0.4)',
                        //                 shadowBlur: 10,
                        //                 shadowOffsetY: 10
                        //             }
                        //         },
                        //         data:groupqtyData[1].data
                        //     },
                        // ]
                    });
                }
                //月
                if(this.ismon){
                    this.mon_chart.setOption({
                        tooltip : {
                            trigger: 'axis',
                            axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                                type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                            }
                        },
                        legend: {
                            data:["恒利搅拌站(销量)", "合辉搅拌站(销量)", "恒利搅拌站(产量)", "合辉搅拌站(产量)"],
                            right: '2%',
                        },
                        grid: {
                            left: '15%',
                            width:'75%',
                            height:'60%',
                            bottom: '17%'
                        },
                        xAxis : [
                            {
                                type: 'category',
                                axisTick: {
                                    alignWithLabel: true
                                },
                                data:['1月','2月','3月','4月','5月','6月','7月','8月','9月','10月','11月','12月']
                            }
                        ],
                        yAxis : [
                            {
                                type: 'value',
                                name:'万(方)',
                                axisTick: {
                                    alignWithLabel: true
                                }
                                //boundaryGap: [0, '100%']
                            },

                            {
                                type: 'value',
                                name:'千(方)'
                            },
                        ],
                        series : groupqtyData
                        // series : [
                        //     {
                        //         name:'搅拌站1',
                        //         type:'line',
                        //         barWidth: '25%',
                        //         itemStyle: {
                        //             normal: {
                        //                 color: '#32c5e9'
                        //             }
                        //         },
                        //         data:[100,500,900,200,400,800,700,600,520,666]
                        //     },
                        //     {
                        //         name:'搅拌站2',
                        //         type:'line',
                        //         barWidth: '25%',
                        //         itemStyle: {
                        //             normal: {
                        //                 color: '#67e0e3'
                        //             }
                        //         },
                        //         data:[100,550,990,100,460,800,550,720,420,566]
                        //     },
                        //     {
                        //         name:'搅拌站3',
                        //         type:'line',
                        //         barWidth: '25%',
                        //         itemStyle: {
                        //             normal: {
                        //                 color: '#9fe6b8'
                        //             }
                        //         },
                        //         data:[200,600,700,300,500,600,800,400,620,756]
                        //     },
                        // ]
                    })
                }
                //年
                if(this.isyear){
                    this.year_chart.setOption({
                        tooltip : {
                            trigger: 'item',
                            formatter: "{a} <br/>{b} : {c} ({d}%)"
                        },
                        legend: {
                            icon: 'circle',
                            left: '50%',
                            //top: '5%',
                            data:["恒利搅拌站(销量)", "合辉搅拌站(销量)", "恒利搅拌站(产量)", "合辉搅拌站(产量)"]
                        },
                        series : [
                            {
                                name: '集团产量信息',
                                type: 'pie',
                                radius : '55%',
                                center: ['25%', '55%'],
                                data:tempDataI,
                                    // [
                                    //     {value:45384, name:'搅拌站1',itemStyle:{normal:{color: '#32c5e9'}}},
                                    //     {value:35144, name:'搅拌站2',itemStyle:{normal:{color: '#67e0e3'}}},
                                    //     {value:71376, name:'搅拌站3',itemStyle:{normal:{color: '#9fe6b8'}}},
                                    // ],
                                label: {
                                    normal: {
                                        formatter: "{c}(万方)",
                                        position: 'inner'
                                    }
                                },
                                labelLine: {
                                    normal: {
                                        lineStyle: {
                                            // color: 'rgba(255, 255, 255, 0.3)'
                                        }
                                    }
                                },
                                itemStyle: {
                                    normal: {
                                        // color: '#f6bfbc',
                                        shadowBlur: 10,
                                        shadowColor: 'rgba(0, 0, 0, 0.5)'
                                    },
                                    emphasis: {
                                        shadowBlur: 15,
                                        shadowOffsetX: 0,
                                        shadowColor: 'rgba(0, 0, 0, 0.5)'
                                    }
                                }
                            },

                            {
                                name: '集团销量信息',
                                type: 'pie',
                                radius : '55%',
                                center: ['75%', '55%'],
                                data:tempDataII,
                                    // [
                                    //     {value:45384, name:'搅拌站1',itemStyle:{normal:{color: '#32c5e9'}}},
                                    //     {value:35144, name:'搅拌站2',itemStyle:{normal:{color: '#67e0e3'}}},
                                    //     {value:71376, name:'搅拌站3',itemStyle:{normal:{color: '#9fe6b8'}}},
                                    // ],
                                label: {
                                    normal: {
                                        formatter: "{c}(万元)",
                                        position: 'inner'
                                    }
                                },
                                labelLine: {
                                    normal: {
                                        lineStyle: {
                                            // color: 'rgba(255, 255, 255, 0.3)'
                                        }
                                    }
                                },
                                itemStyle: {
                                    normal: {
                                        // color: '#f6bfbc',
                                        shadowBlur: 10,
                                        shadowColor: 'rgba(0, 0, 0, 0.5)'
                                    },
                                    emphasis: {
                                        shadowBlur: 15,
                                        shadowOffsetX: 0,
                                        shadowColor: 'rgba(0, 0, 0, 0.5)'
                                    }
                                }
                            }
                        ]
                    })
                }
            }
        }
    }
</script>
<style lang="less" scoped type="text/less">
    .wrap-div{
        height: 100%;
    }
    .bar-chart {
        /*width: 560px;*/
        /*height: 320px;*/
    }
    .mon-chart{
        /*width: 560px;*/
        /*height: 320px;*/
    }
</style>




